import React, { Component } from 'react';
import { Breadcrumb } from 'antd';

import './index.scss';

const breadList = {
  '/gonghao': '',

  '/gonghao/tuoguan': '',
  '/gonghao/tuoguan/create': '',
  '/gonghao/tuoguan/update': '',

  '/gonghao/tuoguan/config': '',
  '/gonghao/tuoguan/config/create': '',
  '/gonghao/tuoguan/config/update': '',

  '/gonghao/material': '',
  '/gonghao/material/create': '',
  '/gonghao/material/update': '',

  '/gonghao/menu': '',
  '/gonghao/menu/create': '',
  '/gonghao/menu/update': '',

  '/kefu': '',

  '/kefu/kMessage': '',
  '/group': '',
  '/group/gMessage': '',
  '/novel': '',
  '/novel/links': '',
  '/novel/spread': '',
  '/novel/adMaterial': '',
  '/novel/channel': '',
  '/novel/tools': '',
  '/novel/novelSpread': '',
  '/novel/backNovel': '',
  '/wechat': '',
  '/wechat/qrCode': '',
  '/wechat/tag': '',
  '/wechat/qiangguan': ''
};

class BreadWrapper extends Component {
  constructor(props) {
    super(props);
    this.state = {
      pathSnippets: null,
      extraBreadcrumbItems: null,
    }
  }

  getPath = () => {
    this.setState({
      pathSnippets: window.location.pathname.split('/').slice(2)
    });
    this.setState((prev) => {
      return {
        extraBreadcrumbItems: prev.pathSnippets.map((_, index) => {
          let url = `/${prev.pathSnippets.slice(0, index + 1).join('/')}`;
          return (
            <Breadcrumb.Item key={url}>
              {breadList[url]}
            </Breadcrumb.Item>
          );
        })
      }
    })
  };

  componentWillMount() {
    this.getPath()
  }

  render() {
    return (
    	<div className='bread-wrapper'>
    		<div className='bread-nav'>
          <Breadcrumb>
            {this.state.extraBreadcrumbItems}
          </Breadcrumb>
        </div>
    	</div>
    )
  }

}

export default BreadWrapper;